* {
  margin: 0px;
  padding: 0px;
}
.weatherComponent {
  display: flex;
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  justify-content: flex-start;
}
.weatherTitle {
  margin: 20px;
  font-family: "Courier New", Courier, monospace;
  font-weight: bolder;
  text-shadow: 2px 2px 5px rgb(235, 235, 235);
  font-style: oblique;
  font-size: 2em;
}
.infoInput {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-direction: column;
  margin: 10px;
  box-shadow: 4px 4px 7px blue;
  position: relative;
  width: 50vw;
  height: 50vh;
  border-radius: 10px;
}
.weatherCityInput {
  background-color: transparent;
  font-size: 1.2em;
  box-shadow: 2px 2px 5px black;
  border-radius: 5px;
  background-color: #65c7f7;
  padding: 5px;
}
.weatherCityInput:focus {
  box-shadow: 3px 4px 5px rgb(247, 247, 247);
}
.weatherInfo {
  position: relative;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 70%;
  border-top: 2px solid blue;
  border-bottom: 2px solid blue;
}
.weathrInfoItem {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70%;
  text-align: center;
  -webkit-text-decoration: wavy;
          text-decoration: wavy;
  font-style: oblique;
  font-family: "Courier New", Courier, monospace;
  font-size: 1.5em;
  font-weight: bolder;
  margin: 5px;
  text-shadow: 2px 1px 3px white;
  text-transform: capitalize;
}
.skeletonContainer {
  display: flex;
  justify-content: flex-start;
  position: relative;
  margin-top: 3px;
}
.detailsButton {
  width: 200px;
  background-color: transparent;
  box-shadow: 2px 2px 4px aqua, -2px -2px 4px aqua;
  border-radius: 5px;
  font-size: 1.3em;
  font-family: "Courier New", Courier, monospace;
  font-style: italic;
  border: 2px solid black;
  font-weight: bolder;
  cursor: pointer;
}
.weatherInfoBk {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  z-index: -1;
  opacity: 0.8;
}
.errContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  position: relative;
  width: 100%;
  height: 70%;
  border-top: 2px solid blue;
  border-bottom: 2px solid blue;
  background-color: orange;
}
.errTitle {
  color: red;
  font-family: "Courier New", Courier, monospace;
  font-style: italic;
  font-weight: bolder;
  font-size: 1.5em;
  text-shadow: 2px 2px 5px black;
  margin: 10px;
}
.errInfo {
  display: block;
  align-items: center;
  justify-content: center;
  margin-top: 10px;
  font-size: 1.2em;
  font-weight: bolder;
  text-shadow: 2px 2px 5px black;
}
.detailsComponents {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: start;
  flex-direction: column;
  width: 50vw;
  height: 50vh;
  background-color: rgb(22, 196, 240);
}
.DetailsNav {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 25%;
  background-color: aliceblue;
  /* box-shadow: 2px 2px 3px black; */
  background-color: transparent;
}
.DetailsNav > span {
  background-color: white;
  box-shadow: 2px 2px 5px rgb(0, 0, 0);
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2em;
  font-weight: bolder;
  font-family: "Courier New", Courier, monospace;
  font-style: italic;
  border-radius: 10px;
  cursor: pointer;
}
.DetailsInfo {
  display: flex;
  width: 100%;
  height: 87%;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.DetailsInfo > div {
  font-size: 1.2em;
  font-weight: bolder;
  font-style: oblique;
  font-family: "Courier New", Courier, monospace;
  margin: 5px;
  text-transform: capitalize;
  text-shadow: 2px 2px 5px black;
}
@media all and (max-width: 500px){
  .infoInput{
    width: 90vw;
  }
}
.weatherBk {
  z-index: -1;
  width: 100vw;
  position: absolute;
  top: 0px;
  right: 0px;
  background: #9cecfb; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #9cecfb,
    #65c7f7,
    #0052d4
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#9cecfb), color-stop(#65c7f7), to(#0052d4));
  background: -webkit-linear-gradient(left, #9cecfb, #65c7f7, #0052d4);
  background: linear-gradient(to right, #9cecfb, #65c7f7, #0052d4);
}


*{
  margin: 0px;
  padding: 0px;
}
.googleMapreact{
  width: 50vw;
  height: 30vh;
}
.CityComponent ,.HotelsComponent {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.HotelsComponent{
  background: -webkit-linear-gradient(
    to right,
    #9cecfb,
    #65c7f7,
    #0052d4
  );
  background: -webkit-gradient(linear, left top, right top, from(#9cecfb), color-stop(#65c7f7), to(#0052d4));
  background: -webkit-linear-gradient(left, #9cecfb, #65c7f7, #0052d4);
  background: linear-gradient(to right, #9cecfb, #65c7f7, #0052d4);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.reactMap{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 40vh;
}
.containerInfoMap{
  width: 100vw;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.HotelThumbnail {
  background-size: cover;
  background-position: center;
}
.image {
  background-position: center;
  background-size: cover;
  width: 50px;
  height: 50px;
}
.HotelsComponent > h1 {
  margin: 20px;
  font-family: "Courier New", Courier, monospace;
  font-weight: bolder;
  text-shadow: 2px 2px 5px rgb(235, 235, 235);
  font-style: oblique;
  font-size: 2em;
}
.parameters{
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 10px;
  border-radius: 7px;
  box-shadow: 2px 2px 5px black;
  padding: 15px;
  background-color: blue;
}
.parameters > input{
  font-size: 1.2em;
  font-family: 'Courier New', Courier, monospace;
  font-style: oblique;
  width: 100px;
  font-weight: bolder;
  /* border: 2px solid black; */
  border-radius: 10px;
}
.parameters >input:focus{
  box-shadow: 3px 3px 5px white;
  border: none;

}
.hotelErr{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px;
  position: relative;
  font-weight: bolder; 
  font-family: 'Courier New', Courier, monospace;
  text-transform: capitalize;
  position: static;
  background-color: blue;
}
.hotelComponent{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
}
.hotelsComponent{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.HotelComponent{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 400px;
  height: 400px;
  background-color: aqua;
  border-radius: 15px;
  margin: 20px;
  box-shadow: 2px 2px 5px black;
  flex-direction: column ;
}
.info{
  width: 100%;
  display: grid;
  padding: 10px;
  grid-template-columns: auto auto;
  font-family: 'Courier New', Courier, monospace;
  font-weight: bolder;
  font-size: larger;
  /* background-color: violet; */
  align-self: center;
}
.info > span  {
text-align: center;
margin: 5px;
display: inline;
}
.footerSkeletonItem{
  width: 300px ;
  height: 20px;
  margin: 20px;
}
.HotelThumbnail{
width: 90%;
height:  80%;
border-radius: 15px;
background-size: cover;
background-position: center;
background-color: khaki;
}
.footer{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 90%;
}
.footer > button{
  width: 50%;
border-radius: 10px;
  background-color: rgb(10, 100, 235);
  color: black;
  cursor: pointer;
  min-height: 30px;
  margin: 15px;
}
.footer >button:hover{
  background-color: black;
  color: white;
  /* box-shadow: 2px 2px 5px rgb(8, 83, 223) , -2px -2px 3px rgb(11, 57, 209) ; */
}
.spanSkeleton{
  display: flex;
  align-items: center;
  justify-content: center;
}
.header{
  display: flex;
  align-items: center;
  justify-content: center;
}
.ratingComponent{
  display: flex;
  /* border: 2px solid red; */
  align-items: center;
  justify-content: center;
}
@media all and (max-width: 500px){
  .parameters{
    padding: 0px;
    flex-direction: column;
   min-height: 250px;
   width: 95vw;
  }
  .reactMap{
    width: 100vw;
  }
  .parameters > input {
min-width: 70vw;
height: 7vh;
border-radius: 20px;
  }
  .parameters > *{
    margin: 0px;
  }
  .googleMapreact{
    width: 100vw;
  }
  .header{
    flex-direction: column;
  }
}
.hotelComponentSkeleton{
background-color: cornsilk;
}
.nav {
  width: 100vw;
  height: 15vh;
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-shadow: 2px 2px 5px black;
}

